"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { 
  CheckSquare, 
  Clock, 
  TrendingUp, 
  Users,
  Calendar,
  FileImage,
  FileText,
  Plus,
  ExternalLink
} from "lucide-react";
import { 
  Project,
  getTasksByProjectId,
  getAssetsByProjectId,
  getContentByProjectId,
  getTeamMemberById
} from "@/lib/utils/dummy-data";
import { format } from "date-fns";
import Link from "next/link";

interface ProjectOverviewProps {
  project: Project;
}

export function ProjectOverview({ project }: ProjectOverviewProps) {
  const projectTasks = getTasksByProjectId(project.id);
  const projectAssets = getAssetsByProjectId(project.id);
  const projectContent = getContentByProjectId(project.id);
  
  const completedTasks = projectTasks.filter(t => t.status === "completed");
  const inProgressTasks = projectTasks.filter(t => t.status === "in_progress");
  const todoTasks = projectTasks.filter(t => t.status === "todo");
  
  const progress = projectTasks.length > 0 ? Math.round((completedTasks.length / projectTasks.length) * 100) : 0;
  
  // Recent activity items (mock data for demonstration)
  const recentActivities = [
    {
      id: "1",
      user: "Alice Johnson",
      action: "completed task",
      target: "Fix homepage header",
      timestamp: "2025-09-11T08:30:00Z"
    },
    {
      id: "2",
      user: "Bob Smith",
      action: "uploaded asset",
      target: "Hero Banner Image",
      timestamp: "2025-09-10T16:45:00Z"
    },
    {
      id: "3",
      user: "Carol Davis",
      action: "created content",
      target: "Blog Post Draft",
      timestamp: "2025-09-10T14:20:00Z"
    }
  ];

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
      {/* Left Column - Main Info */}
      <div className="lg:col-span-2 space-y-6">
        {/* Project Description */}
        <Card>
          <CardHeader>
            <CardTitle>Project Description</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="prose prose-sm max-w-none">
              <p className="text-muted-foreground leading-relaxed">
                {project.description || "No description available for this project. Click edit to add a detailed description of the project goals, requirements, and objectives."}
              </p>
            </div>
            <div className="mt-4">
              <Button variant="outline" size="sm">
                Edit Description
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* Progress Overview */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center space-x-2">
              <TrendingUp className="h-5 w-5" />
              <span>Progress Overview</span>
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="space-y-2">
              <div className="flex items-center justify-between">
                <span className="font-medium">Overall Progress</span>
                <span className="text-2xl font-bold">{progress}%</span>
              </div>
              <Progress value={progress} className="h-3" />
              <p className="text-sm text-muted-foreground">
                {completedTasks.length} of {projectTasks.length} tasks completed
              </p>
            </div>

            <div className="grid grid-cols-3 gap-4">
              <div className="text-center p-4 bg-gray-50 rounded-lg">
                <div className="text-lg font-bold text-gray-600">{todoTasks.length}</div>
                <div className="text-sm text-muted-foreground">To Do</div>
              </div>
              <div className="text-center p-4 bg-blue-50 rounded-lg">
                <div className="text-lg font-bold text-blue-600">{inProgressTasks.length}</div>
                <div className="text-sm text-muted-foreground">In Progress</div>
              </div>
              <div className="text-center p-4 bg-green-50 rounded-lg">
                <div className="text-lg font-bold text-green-600">{completedTasks.length}</div>
                <div className="text-sm text-muted-foreground">Completed</div>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Recent Activity */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <Clock className="h-5 w-5" />
                <span>Recent Activity</span>
              </div>
              <Button variant="ghost" size="sm">
                View All
              </Button>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {recentActivities.map((activity) => (
                <div key={activity.id} className="flex items-center space-x-3">
                  <Avatar className="h-8 w-8">
                    <AvatarImage src={`/avatars/${activity.user.toLowerCase().replace(' ', '-')}.png`} />
                    <AvatarFallback className="text-xs">
                      {activity.user.split(" ").map(n => n[0]).join("")}
                    </AvatarFallback>
                  </Avatar>
                  <div className="flex-1 min-w-0">
                    <p className="text-sm">
                      <span className="font-medium">{activity.user}</span>
                      {" "}{activity.action}{" "}
                      <span className="font-medium">{activity.target}</span>
                    </p>
                    <p className="text-xs text-muted-foreground">
                      {format(new Date(activity.timestamp), "MMM d, h:mm a")}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Right Column - Quick Stats */}
      <div className="space-y-6">
        {/* Quick Stats */}
        <Card>
          <CardHeader>
            <CardTitle>Quick Stats</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <CheckSquare className="h-4 w-4 text-muted-foreground" />
                <span className="text-sm">Tasks</span>
              </div>
              <div className="text-right">
                <div className="font-medium">{projectTasks.length}</div>
                <Link href={`/app/projects/${project.id}?tab=tasks`}>
                  <Button variant="ghost" size="sm" className="h-auto p-0 text-xs">
                    View all
                  </Button>
                </Link>
              </div>
            </div>

            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <FileImage className="h-4 w-4 text-muted-foreground" />
                <span className="text-sm">Assets</span>
              </div>
              <div className="text-right">
                <div className="font-medium">{projectAssets.length}</div>
                <Link href={`/app/projects/${project.id}?tab=assets`}>
                  <Button variant="ghost" size="sm" className="h-auto p-0 text-xs">
                    View all
                  </Button>
                </Link>
              </div>
            </div>

            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <FileText className="h-4 w-4 text-muted-foreground" />
                <span className="text-sm">Content</span>
              </div>
              <div className="text-right">
                <div className="font-medium">{projectContent.length}</div>
                <Link href={`/app/projects/${project.id}?tab=content`}>
                  <Button variant="ghost" size="sm" className="h-auto p-0 text-xs">
                    View all
                  </Button>
                </Link>
              </div>
            </div>

            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <Users className="h-4 w-4 text-muted-foreground" />
                <span className="text-sm">Team Members</span>
              </div>
              <div className="text-right">
                <div className="font-medium">{project.assignedTo?.length || 0}</div>
                <Link href={`/app/projects/${project.id}?tab=team`}>
                  <Button variant="ghost" size="sm" className="h-auto p-0 text-xs">
                    Manage
                  </Button>
                </Link>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Project Team */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <Users className="h-5 w-5" />
                <span>Team</span>
              </div>
              <Button variant="ghost" size="sm">
                <Plus className="h-4 w-4" />
              </Button>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {project.assignedTo?.slice(0, 4).map((memberId) => {
                const member = getTeamMemberById(memberId);
                if (!member) return null;
                
                return (
                  <div key={memberId} className="flex items-center space-x-3">
                    <Avatar className="h-8 w-8">
                      <AvatarImage src={member.avatarUrl} />
                      <AvatarFallback className="text-xs">
                        {member.name.split(" ").map(n => n[0]).join("")}
                      </AvatarFallback>
                    </Avatar>
                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-medium">{member.name}</p>
                      <p className="text-xs text-muted-foreground capitalize">{member.role}</p>
                    </div>
                  </div>
                );
              })}
              {project.assignedTo && project.assignedTo.length > 4 && (
                <div className="text-center pt-2">
                  <Button variant="ghost" size="sm" className="text-xs">
                    +{project.assignedTo.length - 4} more members
                  </Button>
                </div>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Quick Actions */}
        <Card>
          <CardHeader>
            <CardTitle>Quick Actions</CardTitle>
          </CardHeader>
          <CardContent className="space-y-2">
            <Button className="w-full justify-start" variant="outline">
              <Plus className="h-4 w-4 mr-2" />
              Add Task
            </Button>
            <Button className="w-full justify-start" variant="outline">
              <FileImage className="h-4 w-4 mr-2" />
              Upload Asset
            </Button>
            <Button className="w-full justify-start" variant="outline">
              <FileText className="h-4 w-4 mr-2" />
              Create Content
            </Button>
            <Button className="w-full justify-start" variant="outline">
              <ExternalLink className="h-4 w-4 mr-2" />
              Share Project
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}