"use client";

import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Progress } from "@/components/ui/progress";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { 
  ArrowLeft, 
  MoreHorizontal, 
  Settings, 
  Share, 
  Star, 
  Archive,
  Users,
  Calendar,
  Target,
  ExternalLink
} from "lucide-react";
import { 
  Project, 
  getTasksByProjectId, 
  getTeamMemberById 
} from "@/lib/utils/dummy-data";
import { format } from "date-fns";

interface ProjectHeaderProps {
  project: Project;
}

export function ProjectHeader({ project }: ProjectHeaderProps) {
  const projectTasks = getTasksByProjectId(project.id);
  const completedTasks = projectTasks.filter(t => t.status === "completed");
  const progress = projectTasks.length > 0 ? Math.round((completedTasks.length / projectTasks.length) * 100) : 0;

  const getStatusColor = (status: string) => {
    switch (status) {
      case "active":
        return "bg-green-100 text-green-800";
      case "completed":
        return "bg-blue-100 text-blue-800";
      case "archived":
        return "bg-gray-100 text-gray-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  return (
    <div className="space-y-4">
      {/* Breadcrumb and Actions */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-4">
          <Button variant="ghost" size="sm" asChild>
            <Link href="/app/projects">
              <ArrowLeft className="h-4 w-4 mr-2" />
              Back to Projects
            </Link>
          </Button>
        </div>
        
        <div className="flex items-center space-x-2">
          <Button variant="outline" size="sm">
            <Star className="h-4 w-4 mr-2" />
            Star
          </Button>
          <Button variant="outline" size="sm">
            <Share className="h-4 w-4 mr-2" />
            Share
          </Button>
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="outline" size="sm">
                <MoreHorizontal className="h-4 w-4" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end">
              <DropdownMenuLabel>Project Actions</DropdownMenuLabel>
              <DropdownMenuItem>
                <Settings className="mr-2 h-4 w-4" />
                Project Settings
              </DropdownMenuItem>
              <DropdownMenuItem>
                <Users className="mr-2 h-4 w-4" />
                Manage Team
              </DropdownMenuItem>
              <DropdownMenuSeparator />
              <DropdownMenuItem>
                <Archive className="mr-2 h-4 w-4" />
                Archive Project
              </DropdownMenuItem>
              <DropdownMenuItem className="text-red-600">
                Delete Project
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>

      {/* Project Info */}
      <div className="space-y-4">
        <div className="flex items-start justify-between">
          <div className="space-y-2">
            <div className="flex items-center space-x-3">
              <h1 className="text-3xl font-bold tracking-tight">{project.title}</h1>
              <Badge variant="outline" className={getStatusColor(project.status)}>
                {project.status}
              </Badge>
              {project.url && (
                <Button variant="ghost" size="sm" asChild>
                  <Link href={project.url} target="_blank">
                    <ExternalLink className="h-4 w-4" />
                  </Link>
                </Button>
              )}
            </div>
            <p className="text-muted-foreground max-w-2xl">
              {project.description || "No description available for this project."}
            </p>
          </div>
        </div>

{/* Project Stats */}
<div className="grid grid-cols-1 md:grid-cols-2 gap-6">
  {/* Progress */}
  <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
      <div className="flex items-center space-x-2">
        <Target className="h-4 w-4 text-muted-foreground" />
        <span className="text-sm font-medium">Progress</span>
      </div>
              </CardTitle>
            </CardHeader>
    <CardContent className="p-4 space-y-2">

      <div className="space-y-1">
        <div className="flex items-center justify-between">
          <span className="text-2xl font-bold">{progress}%</span>
          <span className="text-sm text-muted-foreground">
            {completedTasks.length}/{projectTasks.length} tasks
          </span>
        </div>
        <Progress value={progress} className="h-2" />
      </div>
    </CardContent>
  </Card>

  {/* Tasks Overview */}
  <Card>

      <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
      <div className="flex items-center space-x-2">
        <Target className="h-4 w-4 text-muted-foreground" />
        <span className="text-sm font-medium">Tasks Overview</span>
      </div>
              </CardTitle>
            </CardHeader>
    <CardContent className="p-4 space-y-2">

      <div className="space-y-1">
        <div className="grid grid-cols-2 gap-2 text-sm">
          <div>
            <div className="font-medium">
              {projectTasks.filter((t) => t.status === "todo").length}
            </div>
            <div className="text-muted-foreground">To Do</div>
          </div>
          <div>
            <div className="font-medium">
              {projectTasks.filter((t) => t.status === "in_progress").length}
            </div>
            <div className="text-muted-foreground">In Progress</div>
          </div>
        </div>
      </div>
    </CardContent>
  </Card>


        </div>
      </div>
    </div>
  );
}